"use client";

import { useState } from 'react';
import { Dialog, DialogContent, DialogHeader, DialogTitle } from '@/components/ui/dialog';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { DashboardWidget, MetricDefinition, FieldDefinition } from '@/lib/dashboard/types';

interface WidgetConfigDialogProps {
  widget: DashboardWidget;
  isOpen: boolean;
  onClose: () => void;
  onSave: (widget: DashboardWidget) => void;
  metrics: MetricDefinition[];
  fields: FieldDefinition[];
}

export function WidgetConfigDialog({
  widget,
  isOpen,
  onClose,
  onSave,
  metrics,
  fields,
}: WidgetConfigDialogProps) {
  const [editedWidget, setEditedWidget] = useState<DashboardWidget>({ ...widget });

  const handleSave = () => {
    onSave(editedWidget);
    onClose();
  };

  const updateConfig = (key: string, value: any) => {
    setEditedWidget({
      ...editedWidget,
      config: {
        ...editedWidget.config,
        [key]: value,
      },
    });
  };

  return (
    <Dialog open={isOpen} onOpenChange={onClose}>
      <DialogContent className="max-w-2xl">
        <DialogHeader>
          <DialogTitle>Configurar Widget</DialogTitle>
        </DialogHeader>

        <Tabs defaultValue="general">
          <TabsList className="grid w-full grid-cols-3">
            <TabsTrigger value="general">Geral</TabsTrigger>
            <TabsTrigger value="data">Dados</TabsTrigger>
            <TabsTrigger value="style">Estilo</TabsTrigger>
          </TabsList>

          <TabsContent value="general" className="space-y-4">
            <div>
              <Label htmlFor="title">T칤tulo</Label>
              <Input
                id="title"
                value={editedWidget.title}
                onChange={(e) =>
                  setEditedWidget({ ...editedWidget, title: e.target.value })
                }
              />
            </div>

            <div>
              <Label>Tipo de Widget</Label>
              <Select
                value={editedWidget.type}
                onValueChange={(value: any) =>
                  setEditedWidget({ ...editedWidget, type: value })
                }
              >
                <SelectTrigger>
                  <SelectValue />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="kpi">KPI</SelectItem>
                  <SelectItem value="bar">Gr치fico de Barras</SelectItem>
                  <SelectItem value="line">Gr치fico de Linha</SelectItem>
                  <SelectItem value="pie">Gr치fico de Pizza</SelectItem>
                  <SelectItem value="table">Tabela</SelectItem>
                </SelectContent>
              </Select>
            </div>
          </TabsContent>

          <TabsContent value="data" className="space-y-4">
            <div>
              <Label>M칠trica</Label>
              <Select
                value={editedWidget.metric}
                onValueChange={(value) =>
                  setEditedWidget({ ...editedWidget, metric: value })
                }
              >
                <SelectTrigger>
                  <SelectValue />
                </SelectTrigger>
                <SelectContent>
                  {metrics.map((metric) => (
                    <SelectItem key={metric.id} value={metric.id}>
                      {metric.name}
                    </SelectItem>
                  ))}
                </SelectContent>
              </Select>
            </div>

            {(editedWidget.type === 'bar' || editedWidget.type === 'pie') && (
              <div>
                <Label>Agrupar por</Label>
                <Select
                  value={editedWidget.config.groupBy}
                  onValueChange={(value) => updateConfig('groupBy', value)}
                >
                  <SelectTrigger>
                    <SelectValue />
                  </SelectTrigger>
                  <SelectContent>
                    {fields
                      .filter((field) => field.groupable)
                      .map((field) => (
                        <SelectItem key={field.id} value={field.id}>
                          {field.name}
                        </SelectItem>
                      ))}
                  </SelectContent>
                </Select>
              </div>
            )}

            {editedWidget.type === 'kpi' && (
              <div>
                <Label>Meta</Label>
                <Input
                  type="number"
                  value={editedWidget.config.target || ''}
                  onChange={(e) =>
                    updateConfig('target', parseFloat(e.target.value))
                  }
                />
              </div>
            )}

            <div>
              <Label>Formato</Label>
              <Select
                value={editedWidget.config.format || 'number'}
                onValueChange={(value) => updateConfig('format', value)}
              >
                <SelectTrigger>
                  <SelectValue />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="number">N칰mero</SelectItem>
                  <SelectItem value="currency">Moeda</SelectItem>
                  <SelectItem value="percentage">Porcentagem</SelectItem>
                </SelectContent>
              </Select>
            </div>
          </TabsContent>

          <TabsContent value="style" className="space-y-4">
            <div>
              <Label>Mostrar Legenda</Label>
              <Select
                value={editedWidget.config.showLegend?.toString() || 'true'}
                onValueChange={(value) =>
                  updateConfig('showLegend', value === 'true')
                }
              >
                <SelectTrigger>
                  <SelectValue />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="true">Sim</SelectItem>
                  <SelectItem value="false">N칚o</SelectItem>
                </SelectContent>
              </Select>
            </div>

            <div>
              <Label>Mostrar Grade</Label>
              <Select
                value={editedWidget.config.showGrid?.toString() || 'true'}
                onValueChange={(value) =>
                  updateConfig('showGrid', value === 'true')
                }
              >
                <SelectTrigger>
                  <SelectValue />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="true">Sim</SelectItem>
                  <SelectItem value="false">N칚o</SelectItem>
                </SelectContent>
              </Select>
            </div>
          </TabsContent>
        </Tabs>

        <div className="flex justify-end gap-2">
          <Button variant="outline" onClick={onClose}>
            Cancelar
          </Button>
          <Button onClick={handleSave}>Salvar</Button>
        </div>
      </DialogContent>
    </Dialog>
  );
}
