"use client";

import { useState, useCallback } from 'react';
import { DragDropContext, Droppable, Draggable } from '@hello-pangea/dnd';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogTrigger } from '@/components/ui/dialog';
import { Badge } from '@/components/ui/badge';
import { Separator } from '@/components/ui/separator';
import {
  Plus,
  Settings,
  Save,
  Eye,
  Trash2,
  GripVertical,
  BarChart3,
  LineChart,
  PieChart,
  Activity,
  Table,
} from 'lucide-react';

import { Dashboard, DashboardWidget, MetricDefinition, FieldDefinition, ChartType } from '@/lib/dashboard/types';
import { MOCK_METRICS, MOCK_FIELDS, CHART_TYPES, MOCK_DASHBOARDS } from '@/lib/dashboard/mock-data';
import { WidgetRenderer } from './widget-renderer';
import { WidgetConfigDialog } from './widget-config-dialog';

interface DashboardBuilderProps {
  userRole: string;
  userId: string;
}

export function DashboardBuilder({ userRole, userId }: DashboardBuilderProps) {
  const [selectedDashboard, setSelectedDashboard] = useState<Dashboard | null>(null);
  const [dashboards, setDashboards] = useState<Dashboard[]>(MOCK_DASHBOARDS);
  const [isCreating, setIsCreating] = useState(false);
  const [newDashboardName, setNewDashboardName] = useState('');
  const [newDashboardDescription, setNewDashboardDescription] = useState('');
  const [selectedWidget, setSelectedWidget] = useState<DashboardWidget | null>(null);
  const [isConfigDialogOpen, setIsConfigDialogOpen] = useState(false);

  const getChartIcon = (type: string) => {
    switch (type) {
      case 'bar': return <BarChart3 className="h-4 w-4" />;
      case 'line': return <LineChart className="h-4 w-4" />;
      case 'pie': return <PieChart className="h-4 w-4" />;
      case 'kpi': return <Activity className="h-4 w-4" />;
      case 'table': return <Table className="h-4 w-4" />;
      default: return <BarChart3 className="h-4 w-4" />;
    }
  };

  const createNewDashboard = () => {
    if (!newDashboardName.trim()) return;

    const newDashboard: Dashboard = {
      id: `dashboard-${Date.now()}`,
      name: newDashboardName,
      description: newDashboardDescription,
      widgets: [],
      filters: [],
      createdBy: userId,
      createdAt: new Date(),
      updatedAt: new Date(),
      isPublic: false,
      allowedRoles: [userRole],
    };

    setDashboards([...dashboards, newDashboard]);
    setSelectedDashboard(newDashboard);
    setIsCreating(false);
    setNewDashboardName('');
    setNewDashboardDescription('');
  };

  const addWidget = (chartType: ChartType) => {
    if (!selectedDashboard) return;

    const newWidget: DashboardWidget = {
      id: `widget-${Date.now()}`,
      type: chartType.id as any,
      title: `Novo ${chartType.name}`,
      metric: chartType.supportedMetrics[0] || 'total_sales',
      filters: [],
      position: {
        x: 0,
        y: selectedDashboard.widgets.length * 2,
        width: chartType.id === 'kpi' ? 3 : 6,
        height: chartType.id === 'kpi' ? 2 : 4,
      },
      config: {
        format: 'number',
        aggregation: 'sum',
      },
    };

    const updatedDashboard = {
      ...selectedDashboard,
      widgets: [...selectedDashboard.widgets, newWidget],
      updatedAt: new Date(),
    };

    setSelectedDashboard(updatedDashboard);
    setDashboards(dashboards.map(d => d.id === updatedDashboard.id ? updatedDashboard : d));
  };

  const updateWidget = (updatedWidget: DashboardWidget) => {
    if (!selectedDashboard) return;

    const updatedDashboard = {
      ...selectedDashboard,
      widgets: selectedDashboard.widgets.map(w => w.id === updatedWidget.id ? updatedWidget : w),
      updatedAt: new Date(),
    };

    setSelectedDashboard(updatedDashboard);
    setDashboards(dashboards.map(d => d.id === updatedDashboard.id ? updatedDashboard : d));
  };

  const deleteWidget = (widgetId: string) => {
    if (!selectedDashboard) return;

    const updatedDashboard = {
      ...selectedDashboard,
      widgets: selectedDashboard.widgets.filter(w => w.id !== widgetId),
      updatedAt: new Date(),
    };

    setSelectedDashboard(updatedDashboard);
    setDashboards(dashboards.map(d => d.id === updatedDashboard.id ? updatedDashboard : d));
  };

  const onDragEnd = (result: any) => {
    if (!result.destination || !selectedDashboard) return;

    const widgets = Array.from(selectedDashboard.widgets);
    const [reorderedWidget] = widgets.splice(result.source.index, 1);
    widgets.splice(result.destination.index, 0, reorderedWidget);

    const updatedDashboard = {
      ...selectedDashboard,
      widgets,
      updatedAt: new Date(),
    };

    setSelectedDashboard(updatedDashboard);
    setDashboards(dashboards.map(d => d.id === updatedDashboard.id ? updatedDashboard : d));
  };

  const saveDashboard = () => {
    if (!selectedDashboard) return;
    // In a real app, this would save to the backend
    console.log('Dashboard saved:', selectedDashboard);
    alert('Dashboard salvo com sucesso!');
  };

  const userDashboards = dashboards.filter(d => 
    d.createdBy === userId || 
    d.allowedRoles.includes(userRole) || 
    d.isPublic
  );

  return (
    <div className="space-y-6">
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-3xl font-bold">Construtor de Dashboards</h1>
          <p className="text-gray-600">Crie e personalize seus dashboards</p>
        </div>
        <div className="flex gap-2">
          {selectedDashboard && (
            <>
              <Button onClick={saveDashboard} className="flex items-center gap-2">
                <Save className="h-4 w-4" />
                Salvar
              </Button>
              <Button variant="outline" className="flex items-center gap-2">
                <Eye className="h-4 w-4" />
                Visualizar
              </Button>
            </>
          )}
        </div>
      </div>

      <div className="grid grid-cols-12 gap-6">
        {/* Sidebar */}
        <div className="col-span-3 space-y-4">
          {/* Dashboard Selection */}
          <Card>
            <CardHeader>
              <CardTitle className="text-lg">Dashboards</CardTitle>
            </CardHeader>
            <CardContent className="space-y-2">
              {userDashboards.map((dashboard) => (
                <div
                  key={dashboard.id}
                  className={`p-3 rounded-lg border cursor-pointer transition-colors ${
                    selectedDashboard?.id === dashboard.id
                      ? 'bg-blue-50 border-blue-200'
                      : 'hover:bg-gray-50'
                  }`}
                  onClick={() => setSelectedDashboard(dashboard)}
                >
                  <div className="font-medium">{dashboard.name}</div>
                  <div className="text-sm text-gray-500">{dashboard.description}</div>
                  <div className="flex items-center gap-1 mt-2">
                    <Badge variant="secondary" className="text-xs">
                      {dashboard.widgets.length} widgets
                    </Badge>
                    {dashboard.createdBy === userId && (
                      <Badge variant="outline" className="text-xs">Meu</Badge>
                    )}
                  </div>
                </div>
              ))}
              
              <Dialog open={isCreating} onOpenChange={setIsCreating}>
                <DialogTrigger asChild>
                  <Button variant="outline" className="w-full">
                    <Plus className="h-4 w-4 mr-2" />
                    Novo Dashboard
                  </Button>
                </DialogTrigger>
                <DialogContent>
                  <DialogHeader>
                    <DialogTitle>Criar Novo Dashboard</DialogTitle>
                  </DialogHeader>
                  <div className="space-y-4">
                    <div>
                      <Label htmlFor="name">Nome</Label>
                      <Input
                        id="name"
                        value={newDashboardName}
                        onChange={(e) => setNewDashboardName(e.target.value)}
                        placeholder="Nome do dashboard"
                      />
                    </div>
                    <div>
                      <Label htmlFor="description">Descri칞칚o</Label>
                      <Input
                        id="description"
                        value={newDashboardDescription}
                        onChange={(e) => setNewDashboardDescription(e.target.value)}
                        placeholder="Descri칞칚o do dashboard"
                      />
                    </div>
                    <div className="flex gap-2">
                      <Button onClick={createNewDashboard} disabled={!newDashboardName.trim()}>
                        Criar
                      </Button>
                      <Button variant="outline" onClick={() => setIsCreating(false)}>
                        Cancelar
                      </Button>
                    </div>
                  </div>
                </DialogContent>
              </Dialog>
            </CardContent>
          </Card>

          {/* Widget Types */}
          {selectedDashboard && (
            <Card>
              <CardHeader>
                <CardTitle className="text-lg">Adicionar Widget</CardTitle>
              </CardHeader>
              <CardContent className="space-y-2">
                {CHART_TYPES.map((chartType) => (
                  <Button
                    key={chartType.id}
                    variant="outline"
                    className="w-full justify-start"
                    onClick={() => addWidget(chartType)}
                  >
                    {getChartIcon(chartType.id)}
                    <span className="ml-2">{chartType.name}</span>
                  </Button>
                ))}
              </CardContent>
            </Card>
          )}
        </div>

        {/* Main Content */}
        <div className="col-span-9">
          {selectedDashboard ? (
            <Card>
              <CardHeader>
                <div className="flex items-center justify-between">
                  <div>
                    <CardTitle>{selectedDashboard.name}</CardTitle>
                    <p className="text-gray-600">{selectedDashboard.description}</p>
                  </div>
                  <Badge variant="outline">
                    {selectedDashboard.widgets.length} widgets
                  </Badge>
                </div>
              </CardHeader>
              <CardContent>
                {selectedDashboard.widgets.length === 0 ? (
                  <div className="text-center py-12">
                    <div className="text-gray-400 mb-4">
                      <BarChart3 className="h-12 w-12 mx-auto" />
                    </div>
                    <h3 className="text-lg font-medium text-gray-900 mb-2">
                      Nenhum widget adicionado
                    </h3>
                    <p className="text-gray-600">
                      Comece adicionando widgets do painel lateral
                    </p>
                  </div>
                ) : (
                  <DragDropContext onDragEnd={onDragEnd}>
                    <Droppable droppableId="widgets">
                      {(provided) => (
                        <div
                          {...provided.droppableProps}
                          ref={provided.innerRef}
                          className="space-y-4"
                        >
                          {selectedDashboard.widgets.map((widget, index) => (
                            <Draggable key={widget.id} draggableId={widget.id} index={index}>
                              {(provided) => (
                                <div
                                  ref={provided.innerRef}
                                  {...provided.draggableProps}
                                  className="bg-white border rounded-lg p-4 shadow-sm"
                                >
                                  <div className="flex items-center justify-between mb-4">
                                    <div className="flex items-center gap-2">
                                      <div {...provided.dragHandleProps}>
                                        <GripVertical className="h-4 w-4 text-gray-400" />
                                      </div>
                                      {getChartIcon(widget.type)}
                                      <span className="font-medium">{widget.title}</span>
                                    </div>
                                    <div className="flex gap-2">
                                      <Button
                                        size="sm"
                                        variant="outline"
                                        onClick={() => {
                                          setSelectedWidget(widget);
                                          setIsConfigDialogOpen(true);
                                        }}
                                      >
                                        <Settings className="h-4 w-4" />
                                      </Button>
                                      <Button
                                        size="sm"
                                        variant="outline"
                                        onClick={() => deleteWidget(widget.id)}
                                      >
                                        <Trash2 className="h-4 w-4" />
                                      </Button>
                                    </div>
                                  </div>
                                  <div className="h-64">
                                    <WidgetRenderer widget={widget} />
                                  </div>
                                </div>
                              )}
                            </Draggable>
                          ))}
                          {provided.placeholder}
                        </div>
                      )}
                    </Droppable>
                  </DragDropContext>
                )}
              </CardContent>
            </Card>
          ) : (
            <Card>
              <CardContent className="text-center py-12">
                <div className="text-gray-400 mb-4">
                  <PieChart className="h-12 w-12 mx-auto" />
                </div>
                <h3 className="text-lg font-medium text-gray-900 mb-2">
                  Selecione um dashboard
                </h3>
                <p className="text-gray-600">
                  Escolha um dashboard existente ou crie um novo
                </p>
              </CardContent>
            </Card>
          )}
        </div>
      </div>

      {/* Widget Configuration Dialog */}
      {selectedWidget && (
        <WidgetConfigDialog
          widget={selectedWidget}
          isOpen={isConfigDialogOpen}
          onClose={() => {
            setIsConfigDialogOpen(false);
            setSelectedWidget(null);
          }}
          onSave={updateWidget}
          metrics={MOCK_METRICS}
          fields={MOCK_FIELDS}
        />
      )}
    </div>
  );
}
